import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import { UserService } from '../../service/user.service';

@Component({
  selector: 'app-basic-information',
  templateUrl: './basic-information.component.html',
  styleUrls: ['./basic-information.component.css']
})
export class BasicInformationComponent implements OnInit {

  attributs = USERATTR;
  attrForm: FormGroup;
  selectedAttribute: string = "Role";

  num_student = 0;
  num_staff = 0;
  num_faculty = 0;
  num_Male = 0;
  num_female = 0;
  num_age10 = 0;
  num_age20 = 0;
  num_age30 = 0;
  num_age40 = 0;
  num_age50 = 0;

  public barChartOptions:any = {
    scaleShowVerticalLines: false,
    responsive: true
  };
  public barChartLabels:string[] = ['Physical', 'Spiritual', 'Relational', 'Behavioral', 'Eco-economical', 'Psychological', 'Overall'];
  public barChartType:string = 'bar';
  public barChartLegend:boolean = true;


  public attributes: string[] = ['Role', 'Gender', 'Age', 'Race', 'Marital Status'];
  public doughnutChartLabels:string[] = ['Student', 'Faculty', 'Staff'];
  public doughnutChartData:number[] = [350, 450, 100];
  public doughnutChartType:string = 'doughnut';

  // events
  public chartClicked(e:any):void {
    console.log(e);
  }

  public chartHovered(e:any):void {
    console.log(e);
  }


  constructor(
    private fb : FormBuilder,
    private userService: UserService,
  ) { }

  ngOnInit() {
    this.userService.getAllUser().subscribe( value => {
        value.forEach(item => {
            if(item.userType == 'Student'){
              this.num_student ++;
            }
            if(item.userType == 'Staff'){
              this.num_staff ++;
            }
            if(item.userType == 'Faculty'){
              this.num_faculty ++;
            }
            if(item.gender == 'Male'){
              this.num_Male ++;
            }
            if(item.gender == 'Student'){
              this.num_female ++;
            }
            if(item.userType == 'Student'){
              this.num_student ++;
            }
            let birthday = new Date(item.birthDate);
            let today = new Date();
            let age = today.getFullYear() - birthday.getFullYear();
            console.log(age);
            if(age < 10){
              this.num_age10 ++;
            }
            else if( age < 20){
              this.num_age20 ++;
            }
            else if(age < 30){
              this.num_age30 ++;
            }
            else if(age < 40){
              this.num_age40 ++;
            }
            else {
              this.num_age50 ++;
            }
            
        });
    })
  }

}

export const USERATTR = [
  {label: 'Role', value: 'Role'},
  {label: 'Gender', value: 'Gender'},
  {label: 'Age', value: 'Age'},
  {label: 'Race', value: 'Race'},
  {label: 'Marital Status', value: 'Marital Status'}
];
